import React from 'react';
import navStyles from './nav.module.css';
import {Link} from 'gatsby';

const Nav = ({siteTitle}) => {
    return(
        <nav id="nav" className={`pt2 absolute dt w-100 center ${navStyles.pos}`}>
            <div className="dtc w5 v-mid ">
                <Link to="/" className="no-underline  f3 fw4 grow pointer dib-ns ph3 ml3 mb3">{siteTitle}</Link>
            </div>
            <div className="dtc tr pt2 ">
                <Link to="/about" className="f5 fw5 grow pointer dn dib-ns pr4 mb3">About</Link>
                {/* <Link className="f5 fw5 grow pointer dn dib-ns pr4 mb3"></Link> */}
            </div>
      </nav>

    );
}

export default Nav;